import React from 'react';
import tokenBc from '../../static/images/token-bc.png';
import { ImgCard } from '../../components/ImgCard/imgCard';
import thumbnail from '../../static/images/dbtb-game-thumbnail.png';
import thumbnail1 from '../../static/images/sdbh_game_thumb_408x314.png';
import thumbnail2 from '../../static/images/dbxv2_game_thumb_408x314_2.png';
import { LayoutBox } from '../../components/layoutBox/layoutBox';
import { useTranslation } from 'react-i18next';
import './token.less';

export function Token() {
  const { t } = useTranslation();
  return (
    <div className="token">
      <LayoutBox imgSrc={tokenBc} title={t('token.title')} h1Text={t('token.h1Text')} h2Text="" />
      <div className="token-bottom">
        <div className="token-img-card-box flex">
          <div className="img-card-box">
            <ImgCard src={thumbnail1} context={t('dragonballNFT')}></ImgCard>
          </div>
          <div className="img-card-box">
            <ImgCard src={thumbnail2} context={t('dragonballNFT')}></ImgCard>
          </div>
          <div className="img-card-box">
            <ImgCard src={thumbnail} context={t('dragonballNFT')}></ImgCard>
          </div>
        </div>
      </div>
    </div>
  );
}
