import React from 'react';
import { useTranslation } from 'react-i18next';
import './CarouselItem.less';

interface carouselItem {
  activeIndex: boolean;
  imgUrl: string;
}

export const CarouselItem = ({ activeIndex, imgUrl }: carouselItem) => {
  const { t } = useTranslation();

  return (
    <div className={activeIndex ? 'activeCard carousel-item-box flex' : 'carousel-item-box flex'}>
      <div className="carousel-item-backImg" style={{ backgroundImage: `url(${imgUrl})` }}></div>
      <div className="carousel-item-content">
        <h2 className="content-text">{t('home.dragonball')}</h2>
        <div className="content-btn">{t('home.playnow')}</div>
      </div>
    </div>
  );
};
