import React from 'react';
import './teamMembers.less';
import { teamMemberItem, teamMemberList } from './constants';
import { useTranslation } from 'react-i18next';

export function TeamMember() {
  const [active, setActive] = React.useState(0);
  const { t } = useTranslation();
  const i18nTeamMemberList: teamMemberItem[] = t('team.teamMemberList', { returnObjects: true });
  const renderi18nTeamMemberList = teamMemberList.map((member, i) => {
    return {
      imgSrc: member.imgSrc,
      ...i18nTeamMemberList[i]
    };
  });
  const activeInfo = renderi18nTeamMemberList[active];

  return (
    <div className="teamMembers flex">
      <div className="TeamMember-display flex">
        <div className="TeamMember-display-img">
          <img src={activeInfo.imgSrc} alt="" />
        </div>
        <div className="TeamMember-display-info flex">
          <div className="TeamMember-display-info-name">{activeInfo.name}</div>
          <div className="TeamMember-display-info-position">{activeInfo.position}</div>
          <div className="TeamMember-display-info-description">{activeInfo.description}</div>
        </div>
      </div>
      <div className="TeamMember-list">
        {renderi18nTeamMemberList.map(({ imgSrc, name }, index) => {
          return (
            <div
              key={index}
              className={active === index ? 'TeamMember-list-item active-TeamMember-list-item' : 'TeamMember-list-item'}
              onClick={() => setActive(index)}
            >
              <div className="TeamMember-list-item-img">
                <img src={imgSrc} alt="" />
              </div>
              <div className="TeamMember-list-item-name flex">
                <span>{name}</span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
