import React from 'react';
import styles from './app.module.less';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import { Header } from '../components/header/header';
import { Footer } from '../components/footer/footer';
import { Home } from '../pages/home/home';
import { Token } from '../pages/token/token';
import { GameGuide } from '../pages/gameGuide/gameGuide';
import { AboutTeam } from '../pages/aboutteam/aboutteam';
import { Referral } from '../pages/referral/referral';
import { User } from '../pages/user/user';

import { Characters } from '../pages/gameGuide/components/characters';

import 'antd/dist/antd.css';
import { LoadingAnimationProvider } from '../components/loadingAnimationProvider/loadingAnimationProvider';

export function App() {
  return (
    <BrowserRouter>
      <div className={styles.app}>
        <Header />
        <LoadingAnimationProvider>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/Token" element={<Token />} />
            <Route path="/about-team" element={<AboutTeam />} />
            <Route path="/referral" element={<Referral />} />
            <Route path="/user" element={<User />} />
            <Route path="/GameGuide/*" element={<GameGuide />}></Route>
            <Route path="*" element={<Home />} />
          </Routes>
        </LoadingAnimationProvider>
        <div className="body-back"></div>
        <Footer />
      </div>
    </BrowserRouter>
  );
}

export default App;
