import React from 'react';
import referralbc from '../../static/images/referral-bc.jpg';
import { LayoutBox } from '../../components/layoutBox/layoutBox';
import { PageTitle } from '../../components/pagesTitle/pageTitle';
import { useTranslation } from 'react-i18next';
import './referral.less';

export function Referral() {
  const {t} = useTranslation();
  return (
    <div className="Referral">
      <LayoutBox
        imgSrc={referralbc}
        title={t('referral.title')}
        h1Text={t('referral.h1')}
        h2Text={t('referral.h2')}
      />
      <div className="Referral-main">
        <PageTitle title={t('referral.font')}></PageTitle>
      </div>
    </div>
  );
}
