import React from 'react';
export function Skills() {
  return (
    <div>
      <div id="wrapper">
        <div id="navigation">
          <div id="page-body">
            <div className="container">
              <div className="text-center title">Skills</div>

              <div className="GameGuideContainer">
                <div className="navigation"></div>

                <div className="content">
                  <div id="ggSkillsMenu">
                    <h2>Skills Menu</h2>

                    <p>
                      To see what skills your className has, press the "K" button. In there you will be able to see the
                      normal className and it's master className skills. You will also be able to see the "Action
                      Skills" with all the different actions that a character can do.
                    </p>

                    <p>
                      The image shown above is known as a "Skill Tree". In a Skill tree you are able to learn one skill
                      after the other, and you can level up each skill to make it stronger. To level up a skill you must
                      press the "+" button on the skill in the skill tree. Once you learn a new skill or level up a
                      skill it will consume 1 "Skill Point" each time, so be careful when using the skill points.
                    </p>
                    <p>
                      To use the skills easily you can drag them over from the skill tree to the short cut keys bar
                      located at the centre bottom of the screen. To find out what the effects of a skill are, you
                      simply hover over the skill and it will show all information about the skill. For example the
                      Cool-Down time, the effect duration etc.
                    </p>
                    <p>
                      Passive skills are located on the right hand side of a skill tree. Depending on which className
                      you have you will have different passive skills according to the className. Learning the passive
                      skills will make the player stronger in one way or another and they are not to be used like the
                      normal skills. Some of the passive skills require skill points to be learned, other passive skills
                      are obtained by questing and some are obtained by wishes from shenron.
                    </p>
                    <p>
                      Finally comes the Action Skills which contain things like pick up item skill, open private shop
                      skill, dicing skill etc and many action skills like laughing stance etc. These skills can also be
                      put into the short cut keys bar.
                    </p>
                  </div>
                </div>

                <div className="content">
                  <div id="ggUpgradeSkills">
                    <h2>Upgrading Skills</h2>

                    <p>
                      Skill Points can be used to learn or upgrade skills by pressing the "+" key on the skill. Learn or
                      upgrade will always consume 1 SP.
                    </p>

                    <p>
                      Move your mouse over the skill icon, or move to "+", to confirm the current skill level, as well
                      as the possibility of upgrading it.
                    </p>
                  </div>
                </div>

                <div className="content">
                  <div id="ggRpUtilization">
                    <h2>RP Utilization</h2>

                    <p>
                      In Dragon Ball Online Global, It is essential to make use of your RP abilities to perform well in
                      combat.{' '}
                    </p>
                    <p>
                      RP effects can used to greately boost an aspect of your skills. Once the player learns the "Power
                      up" skill, you can begin to charge RP balls by holding down the "Z" key (default). Each time a
                      player levels up to a multiple of 10, they will gain an extra RP ball slot.{' '}
                    </p>
                    <p>RP will gradually reduce over time when not in combat mode.</p>
                    <p>
                      If you have enough RP balls you can activate an RP effect to strengthen a skill, when an RP effect
                      is activated the player will charge for a short moment and then unleash their powered up skill!{' '}
                    </p>
                    <p>
                      Press and hold the skill key let a Skill Attribute selection appear, then click with your mouse,
                      or press the assigned key to activate the effect. If you do not select an option whitin 2 seconds,
                      it will cancel the skill. An RP ball will be consumed every time you use an RP effect.
                    </p>

                    <p>The RP system consists of 7 different effects you can choose from to power up your skills.</p>

                    <p>
                      Open the skill list, you can press the icon next to each skill to set the RP effect, then simply
                      hold down the assigned button on your skill hotbar, it will automatically activate the chosen RP
                      effect with your skill.
                    </p>
                  </div>
                </div>

                <div className="content">
                  <div id="ggTransformationSkill">
                    <h2>Transformation Skill</h2>

                    <div className="table-responsive">
                      <table className="table">
                        <tbody>
                          <tr>
                            <th style={{'width':'15%'}}>Race</th>
                            <th>Transformation</th>
                          </tr>
                          <tr>
                            <td>Human</td>
                            <td>Super Saiyan</td>
                          </tr>
                          <tr>
                            <td>Namekian</td>
                            <td>Great Namek</td>
                          </tr>
                          <tr>
                            <td>Majin</td>
                            <td>Pure Majin</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <p>
                      Transformation skills can consume EP or hold up for a special time, if youe EP reachs 10% or the
                      time is up, you will return to your narmal state. Cooldown time for the transformation skill will
                      be 1 hour.
                    </p>

                    <h3>Super Saiyan</h3>
                    <p>
                      Transform into a Super Saiyan, your hairstyle will change and your body will emit a strong energy
                      (and glow too!). In this state, your movement speed and attack speed will increase along with your
                      EP increasing to help combat the EP drain. When Super Saiyan is de-activated or wares off, your
                      character will need a few seconds to catch their breath.
                    </p>
                    <h3>Great Namek</h3>
                    <p>
                      Only Namekians can utilize this skill to its fullest potential, the transformed characters size
                      will increase rapidly. The characters speed will slow down due to the massive increase in size.
                      Just as you would expect, their attack will be boosted along with the size of their body. In this
                      state, only specific basic attacks, or special skills will work.
                    </p>
                    <h3>Pure Majin</h3>
                    <p>
                      The transformation will wake up the sleeping potential hiding inside of a Majin's body. Their
                      defense and attack power will significantly increase, but their speed will become slower. In this
                      state, only specific high power skills and other skills will work.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
