import React from 'react';
export function DojoSystem() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Dojo</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>Dojo</li>
                  <li>
                    <a>Dojo scramble</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggDjojo">
                  <h2>Dojo</h2>

                  <p>
                    When your Guild grows to a certain size you will be able to buy the rights to a Dojo. It will become
                    a place where only members of your Guild can enter. You can play away from the attention of others.
                    Inside you can find Dojo only NPCs which you will have earned the right to interact with.{' '}
                  </p>

                  <div>
                    <h2 className="Bstyle">Conditions to own rights to a Dojo: </h2>
                    <ul className="List_a">
                      <li>Have at least 10,000 Guild Points</li>
                      <li>Have at least 5,000,000 Zeni to pay for it</li>
                    </ul>
                    <p>
                      To gain ownership of a Dojo the Guild Master must talk to the Guild Manager NPC and apply for Dojo
                      Ownership. There are a total of seven Dojo in the world of Dragon Ball Online Global.
                    </p>
                    <h2>Dojo Locations</h2>
                    <ul className="List_a" id="inline">
                      <li>Kokkara Village</li>
                      <li>Porunga Rocks North</li>
                      <li>Fran Fran Desert North</li>
                      <li>Korin Village</li>
                      <li>West City / Westland</li>
                      <li>Buu Memorial</li>
                      <li>Mushroom Rocks North</li>
                    </ul>
                    <table cellPadding="0" cellSpacing="0" className="bd0">
                      <tbody>
                        <tr>
                          <td>
                            <h6>
                              <a className="group" href="/style/images/doj01.jpg">
                                <img src="/style/images/doj01.jpg" width="249" height="269" />
                              </a>
                            </h6>
                          </td>
                          <td width="250" valign="top">
                            <ul>
                              <li>
                                Only a Guild Master can apply to own a Dojo. As long as a Dojo is not yet currently
                                owned by another Guild, you will be able to apply directly to the Guild Manager NPC.
                                Once your Guild owns a Dojo the badge on the front of the Dojo will display your Guilds
                                Emblem.
                              </li>
                            </ul>
                          </td>
                        </tr>
                      </tbody>
                    </table>

                    <h4>
                      If you own a Dojo both the Guild Master and one chosen member of the Guild will automatically be
                      entered into the World Martial Arts Tournament without having to compete in the preliminaries.
                    </h4>

                    <h3>Dojo Introduction</h3>
                    <ul>
                      <li>
                        The Dojo is broken into <span className="orangeB">5 separate areas</span>. Here members can use
                        each section to train and practice tactics or hang out and take photos. There are also merchants
                        inside to buy and sell items with.
                      </li>
                    </ul>
                    <h6>
                      <a href="/style/images/doj02.jpg" className="group">
                        <img src="/style/images/doj02.jpg" width="287" height="340" />
                      </a>
                    </h6>
                    <ul>
                      <li>
                        The supply room inside of a Dojo is used for buying and selling items that can only be obtained
                        inside of a Dojo. There is also a Dojo Bank that the Guild Master can access.
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div className="content">
                <div id="ggdojoscramble">
                  <h2>Dojo Scramble</h2>

                  <p>
                    Since the world only has a total of seven Dojo, it's very likely that they will be occupied by other
                    guilds. If you want to take over the guild, you must prove you deserve it via an act of war. Fight
                    for the right to have your name on the wall!
                  </p>

                  <div>
                    <h4>Dojo Scramble Rules</h4>

                    <p>
                      Dojo Scramble is only available to Guilds that have the properties to own a Dojo in the first
                      place. To challenge a Dojo for ownership the Guild Master must send 1,000,000 Zeni along with
                      their application to fight. Dojo Scramble starts at 6:00AM. When Dojo Scramble invitations are
                      sent out, the following things happen:
                    </p>

                    <h6>
                      <a className="group" href="/style/images/dojo01.jpg">
                        <img src="/style/images/dojo01.jpg" width="555" height="326" />
                      </a>
                    </h6>

                    <h5>
                      The Guild Master that owns the Dojo will receive a challenge notification. They can accept the
                      challenge to defend their Dojo or refuse it and pay 100,000,000 Zeni as compensation.
                    </h5>
                  </div>
                </div>
              </div>

              <div>
                <div>
                  <div>
                    <h2>Dojo Scramble Rules</h2>
                    <p>
                      At the start of the battle, each Guild window will appear on-screen. Players wishing to take part
                      in the Scramble can choose to enter the fight from the window. Up to 30 Players from each side can
                      jump in to fight for their guilds honour.
                    </p>
                    <p>
                      The time limit for Dojo Scramble is 1 hour (60 Minutes). Both the offensive and defensive sides
                      will need to collect 500 points to win. If the timer runs out before a side has collected 500
                      points then the Guild with the most points will win. If both Guilds have the same amount of points
                      when the timer is up, the defending team will retain ownership of their Dojo.
                    </p>
                    <h6 id="NS">
                      <img src="/style/images/dojo02.jpg" width="400" height="400" />
                    </h6>

                    <h3>How to obtain points</h3>
                    <table className="bd0" cellSpacing="0" cellPadding="0">
                      <tbody>
                        <tr>
                          <td>
                            <h6>
                              <a className="group" href="/style/images/dojo03.jpg">
                                <img src="/style/images/dojo03.jpg" width="197" height="276" />
                              </a>
                            </h6>
                          </td>

                          <td valign="top">
                            <ul>
                              <li>
                                At the start of a Dojo Scramble, every participant will be given a "Seal Controller" in
                                their inventory. This will be used in each of the 4 rooms, The Self Training Chamber,
                                The Master Chamber, The Supply Chamber, The Reception Chamber, and the Guild Masters
                                room. Players must travel to each of the rooms and 'capture' the seal which is located
                                in said rooms. When you own a seal you will obtain 2 points, you will gain more points
                                the longer you own the seal and gain more as you gain ownership of more seals.
                              </li>

                              <li>
                                Each Guild will start at the north or south sides of the Dojo. When a player is killed
                                by someone on the opposing team they will respawn back to the side their team started
                                at. When the Dojo Scramble event finishes all players who are on the winning team will
                                stay where they currently are while the ownership of the Dojo is changed and the losing
                                team will be forced outside.
                              </li>
                            </ul>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
