import React from 'react';
import './user.less';
import { UserConsole } from '../../components/userConsole/userConsole';
import { Button, Input, message } from 'antd';
export function User() {
  const register = () => {
    message.success({
      content: '注册成功',
      className: 'custom-class',
    });
    
  };
  return (
    <div className="user-box">
      <div className="user">
        <div className="backgroud-text-box">
          <div className="backgroud-text  dragon-text">DRAGON</div>
          <div className="backgroud-text">BALL COIN</div>
        </div>
        <div className="user-console-box">
          <div className="user-console-item">
            
            <div className="user-console-item-title flexcenter between">
              <span className="">WALLET</span>
              <span className="flexcenter ">
                <Button className="btn" type="primary">
                  connect wallet
                </Button>
                
              </span>
            </div>
            <div className="user-console-item-text">Number</div>
            <div className="user-console-item-text ">1231351154AXDD25545卡号</div>
          </div>
          <div className="user-console-item">
            <div className="user-console-item-title flexcenter between">
              <span className="">REGISTER</span>
              <span className="flexcenter ">
                <Button className="btn" type="primary">
                  approve
                </Button>
                <Button onClick={() => register()} className="register-btn btn" type="primary">
                  register
                </Button>
              </span>
            </div>
            <div className="user-console-item-text">
              <Input className="input" placeholder="Basic usage" />
            </div>
            <div className="user-console-item-text">
              <div>account:XXX账号</div>
              <div>password:XXXX密码</div>
            </div>
          </div>
          <div className="user-console-item ">
            <div className="user-console-item-title colororange">RECHARGE</div>
            <div className="user-console-item-text">
              <Input className="input" placeholder="Basic usage" />
            </div>
            <div className="user-console-item-text">
              <Button className="btn" type="primary">
                recharge
              </Button>
            </div>
          </div>
          <div className="user-console-item">
            <div className="user-console-item-title colororange">WITHDRAW</div>
            <div className="user-console-item-text">
              <Input className="input" placeholder="Basic usage" />
            </div>
            <div className="user-console-item-text">
              <Button className="btn" type="primary">
                withdraw
              </Button>
            </div>
          </div>
          {/* <UserConsole></UserConsole> */}
        </div>
      </div>
    </div>
  );
}
