import React from 'react';
import homeBc from '../../static/images/home-bc.png';
import { LayoutBox } from '../../components/layoutBox/layoutBox';
import { TeamMember } from './components/teamMembers/teamMember';
import { PageTitle } from '../../components/pagesTitle/pageTitle';
import { useTranslation } from 'react-i18next';
import './aboutTeam.less';

export function AboutTeam() {
  const {t} = useTranslation();
  return (
    <div className="aboutTeam">
      <LayoutBox imgSrc={homeBc} title={t('token.title')} h1Text={t('header.title')} h2Text="" />
      <div className="teamMember-box">
        <div className="teamMember-content">
          <PageTitle title={t('team.title')}>{t('team.text')}</PageTitle>
          <TeamMember />
        </div>
      </div>
    </div>
  );
}
