import React from 'react';
import './footer.less';
import { Link } from 'react-router-dom';
import { logo, iconList } from './constants';

export const Footer = () => (
  <footer>
    <div className="footer-top footer-box">
      <div className="footer-content flex">
        <div className="footer-logo">
          <img src={logo} alt="" />
        </div>
        <div className="link-box">
          <div className="link-title">LINKS</div>
          <div className="link-text flex">
            <div className="link-list flex">
              <Link className="link-item" to="/token">
                Token
              </Link>
              <Link className="link-item" to="/about-team">
                Our Team
              </Link>
            </div>
            <div className="link-list flex">
              {iconList.map((icon, i) => (
                <a target='blank' key={i} className="link-item" href={icon.href}>
                  <img className="icon" key={i} src={icon.src} alt="icon" />
                  <span>{icon.text}</span>
                </a>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div className="footer-button footer-box">
      <div className="footer-content flex footer-bottom-content">
        <div className="footer-bottom-left ">© Copyrights, 2021 All Rights Reserved</div>
        <div className="footer-bottom-icon flex">
          {iconList.map((icon, i) => (
            <a target='blank' key={i} className="link-item" href={icon.href}>
              <img className="icon" key={i} src={icon.src} alt="icon" />
            </a>
          ))}
        </div>
      </div>
    </div>
  </footer>
);
