import React from 'react';
export function Movesystem() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Move System</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>World Map</li>
                  <li>
                    <a>Map Revealers</a>
                  </li>
                  <li>
                    <a>Yardrat Teleportation</a>
                  </li>
                  <li>
                    <a>Personal Vehicles</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggWorldMap">
                  <h2>World Map</h2>
                  <p>
                    In Dragon Ball Online Global, Players will start in their races respective map. Yahoi West (Human),
                    North Porunga Rocks (Namekian), and North Fran Fran Desert (Majin), Your goal will be to grow
                    stronger while travelling to new and exciting places.
                  </p>

                  <p>World Map</p>
                  <img src="./style/images/move_sys_map_01.jpg" width="300px" />
                  <p>Yahoi West(Human)</p>
                  <img src="./style/images/move_sys_map_02.jpg" width="300px" />
                  <p>Porunga Rocks North (Namekian)</p>
                  <img src="./style/images/move_sys_map_03.jpg" width="300px" />
                  <p>North Fran Fran Desert (Majin)</p>
                  <img src="./style/images/move_sys_map_04.jpg" width="300px" />
                  <p>Korin Forest</p>
                  <img src="./style/images/move_sys_map_05.jpg" />
                  <p>Westland</p>
                  <img src="./style/images/move_sys_map_06.jpg" />
                  <p>Yahoi East</p>
                  <img src="./style/images/move_sys_map_07.jpg" />
                  <p>Mushroom Rocks North</p>
                  <img src="./style/images/move_sys_map_08.jpg" />
                  <p>Mushroom Rocks South</p>
                  <img src="./style/images/move_sys_map_09.jpg" />
                  <p>Porunga Rocks South</p>
                  <img src="./style/images/move_sys_map_10.jpg" />
                  <p>Papaya Island</p>
                  <img src="./style/images/move_sys_map_11.jpg" />
                  <p>Fear Land (Hidden Map)</p>
                  <img src="./style/images/move_sys_map_12.jpg" />
                </div>
              </div>

              <div className="content">
                <div id="MapRevealers">
                  <h2>Map Revealers</h2>
                  <p>
                    When you enter the world of Dragon Ball Online Global only a small amount of your world map will be
                    revealed for you to see. Throughout your adventure, you will come across some Map Revealers. These
                    will light up sections of your World Map for you.
                  </p>

                  <div className="OneTwo">
                    <div className="left">
                      <img src="./style/images/move_sys_map_revealer_01.jpg" width="300" style={{ border: '0' }} />
                    </div>

                    <div className="right">
                      <p>Sections of your map will be hidden at the beginning.</p>
                    </div>
                  </div>

                  <p>Find the Map Revealers, Right clicking on these will reveal a section of your World Map.</p>
                  <img src="./style/images/move_sys_map_revealer_02.jpg" width="300" />

                  <div className="OneTwo">
                    <div className="left">
                      <img src="./style/images/move_sys_map_revealer_03.jpg" width="300" />
                    </div>

                    <div className="right">
                      <p>The hidden section of the map will be revealed!</p>
                    </div>
                  </div>
                </div>
              </div>

              <div className="content">
                <div id="ggYardratTeleportations">
                  <h2>Yardrat Teleportations</h2>
                  <p>
                    The world of Dragon Ball Online Global is a vast one. At times you may wish to take your time and
                    look at the beautiful wonders of the world. But others you may just want to get from point A to
                    point B quickly and with no hassle. It is times like this where you can use the Yardrat
                    Teleportation Service. Simply speak to one of the many Teleportation Service workers placed around
                    the world and select one of the many Teleport spots you've visited before, Pay the small transport
                    fee to be teleported to your designated area.
                  </p>
                  <h4>How to use Yardrat Teleportation</h4>
                  <p>
                    Each main village as well as various other spots around the world, There will be a Teleportation
                    Service worker standing near their energy machine.
                  </p>
                  <img src="./style/images/move_sys_teleporter_01.jpg" width="500" />
                  <p>
                    To unlock these teleportation icons you must first visit the Yardrat at said area. Then you can
                    teleport back there whenever you visit the Teleportation Service again. Do not forget to scout out
                    the Yardrat each time you visit a new place!
                  </p>
                  <img src="./style/images/move_sys_teleporter_02.jpg" width="500" />
                  <p>
                    To use the service, right click on the Yardrat worker and select teleport. Once the world map opens,
                    you will see all locations that are available to your player. Select where you wish to travel to and
                    confirm the payment. You will be moved there immediately.
                  </p>
                  <img src="./style/images/move_sys_teleporter_03.jpg" width="500" />
                  <p>
                    The teleportation fee will increase with the distance traveled. Be sure to keep track of your
                    spending!
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggPersonalVehicles">
                  <h2>Personal Vehicles</h2>
                  <p>
                    Although bus routes are the fastest way to get around, they can only follow set paths. Should you
                    want to go somewhere else and do it faster than walking, you can rent your own Personal Vehicle. In
                    Dragon Ball Online Global you do not need a drivers license!
                  </p>
                  <p>
                    Personal You can rent vehicles at specific merchants or buy them from the cash shop. You will be
                    able to board your vehicle as many times as you want during the lease period. Some vehicles have a
                    default speed faster than that of others. While riding your vehicle you will be able to add
                    different types of fuel to get that extra boost of speed!
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td width="45%" align="left" valign="top">
                          <img src="./style/images/move_sys_car_01.jpg" width="400" />
                        </td>
                        <td width="55%" align="left" valign="top">
                          <h3>Boarding:</h3>
                          <p>
                            Right click on the vehicle icon in your inventory. That will place you on or inside of the
                            chosen mode of transport.{' '}
                          </p>
                          <h3>Movement:</h3>
                          <p>
                            You will move the same way you would normally while using the vehicles. Use the W / A / S /
                            D keys to move. A / D to turn left and right while W / S to move forward and backward.{' '}
                          </p>
                          <h3>Dismounting:</h3>
                          <p>
                            To dismount your vehicle you can press the dismount button on the vehicle interface, or by
                            pressing the Esc key on your keyboard. If you enter combat mode you will be forced off from
                            your mount instantly.
                          </p>
                        </td>
                      </tr>
                    </table>
                  </div>

                  <p>
                    The rental period of your vehicle will start to decrease from the moment you purchase it. Even when
                    you are not online the timer will decrease. Once the timer hits 0 you can then renew the rental
                    period or discard the items.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
