import React from 'react';
import { Link } from 'react-router-dom';
import { Menu } from 'antd';
const leftNavList: Array<{
  path: string;
  title: string;
}> = [
  { path: 'Characters', title: `Characters` },
  { path: 'Dungeons', title: `Dungeons` },
  { path: 'Skills', title: `Skills` },
  { path: 'Storyline', title: `Storyline` },
  { path: 'Community', title: `Community` },
  { path: 'TradeSystem', title: `Trade System` },
  { path: 'NPCs', title: `NPC's` },
  { path: 'CombatSystem', title: `Combat System` },
  { path: 'DragonBalls', title: `Dragon Balls` },
  { path: 'RankingSystem', title: `Ranking System` },
  { path: 'Crafting', title: `Crafting` },
  { path: 'MoveSystem', title: `Move System` },
  { path: 'WorldSystem', title: `World System` },
  { path: 'MascotSystem', title: `Mascot System` },
  { path: 'DojoSystem', title: `Dojo System` },
  { path: 'ScouterSystem', title: `Scouter System` },
  { path: 'ItemSystem', title: `Item System` },
  { path: 'CashShop', title: `Cash Shop` },
  { path: 'TokenSystem', title: `Token System` },
  { path: 'BudokaiSystem', title: `Budokai System` },
  
  { path: 'EventSystem', title: `Event System` },
  { path: 'TitleSystem', title: `Title System` },
  { path: 'DogiSystem', title: `Dogi System` },
  
];
export function LeftNav() {
  return (
    <div className="LeftNav" id="sidebar">
      <div className="list-group">
        <Menu>
          {leftNavList.map((item, i) => {
            return (
              <Menu.Item key={i}>
                <Link to={`/GameGuide/${item.path}`}> {item.title}</Link>
              </Menu.Item>
            );
          })}
        </Menu>
      </div>
    </div>
  );
}
