import React from 'react';
export function Crafting() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Crafting</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>Crafting Masters</li>
                  <li>
                    <a>HoiPoi Machines</a>
                  </li>
                  <li>
                    <a>Crafting Levels</a>
                  </li>
                  <li>
                    <a>Crafting Recipes</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggCraftingMasters">
                  <h2>Crafting Masters</h2>

                  <p>
                    In Age 995, Capsule Corporation felt the need to popularise hybrid capsule technology. The "HoiPoi
                    Capsule Mix Association" was established! 200 of the best Capsule Corporations scientists banded
                    together to share their skills and technology with the world. They helped to create the "CMA HoiPoi
                    Capsule Crafting" machines for all to use. CMA employees are always on the lookout to teach new
                    players the basics of crafting!
                  </p>
                  <p>
                    Crafting Hybrid capsules is somewhat complicated due to the highly developed technology involved.
                    Luckily the CMA has designed a Mixing Machine to do the hard part for us! the following things can
                    be crafted using the machine:{' '}
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td width="242">
                          <img src="./style/images/mixcapsule_crafting_01.jpg" width="200" />
                        </td>
                        <td width="356">
                          <p>
                            Crafting Hybrid capsules is somewhat complicated due to the highly developed technology
                            involved. Luckily the CMA has designed a Mixing Machine to do the hard part for us! the
                            following things can be crafted using the machine:{' '}
                          </p>
                          <ul>
                            <li>Main Weapons (Gloves and Staffs)</li>
                            <li>Sub-Weapons (Claws, Stick, Sword, Mask etc.)</li>
                            <li>Jacket</li>
                            <li>Trousers</li>
                            <li>Boots</li>
                            <li>Necklaces</li>
                            <li>Earrings</li>
                            <li>Rings</li>
                            <li>Scouters</li>
                          </ul>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>

              <div className="content">
                <div id="ggHoiPoiMachines">
                  <h2>HoiPoi Machines</h2>

                  <p>
                    The main type of crafting machine you will be using are the ones owned by the Crafting Masters,
                    typically located right next to or behind them. Since the machines are large you will only find them
                    in major cities where there is sufficient space for them.
                  </p>
                  <p>
                    The second type of HoiPoi machine you will use is the portable HoiPoi Machine, this will fit in your
                    pocket via a Dinocap and you will be able to use it whenever and wherever you are on your travels.
                  </p>
                  <img src="./style/images/mixcapsule_portable_crafting_01.jpg" />
                  <p>
                    Once you are finished using the portable HoiPoi Machine please remember to close the Crafting
                    window.
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggCraftingLevels">
                  <h2>Crafting Levels</h2>

                  <p>
                    Before you go wild and think you can craft anything and everything. The CMA have set up a system to
                    prevent explosions and personal injuries to crafters and their peers. Currently when you start
                    crafting you will be "craft level 1" with this you can create the most basic of weapons and armour.
                    With each successful craft, you will gain Craft EXP and eventually level up! Every 5 Craft levels
                    you gain will unlock the ability to create higher level gear for you and your friends!
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td>
                          <img src="./style/images/mixcapsule_crafting_level_01.jpg" width="200" />
                        </td>
                        <td>
                          <p>
                            You can check your current Crafting level and Craft EXP bar in the "Craft" tab of your
                            character window (To open the character window press the "C" key){' '}
                          </p>
                        </td>
                      </tr>
                    </table>
                  </div>

                  <p>
                    Now that you understand how the Craft level system works, you must learn about Crafting Recipes.
                    There are many recipes to be found all across the world! Players can buy basic recipes from the
                    "Crafting Masters" and can even obtain secret and rare recipes from event NPC characters! Be careful
                    not to buy a recipe above your current level. You will not be able to use it until your craft level
                    is equal to or higher to that of the recipe.
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggCraftingRecipes">
                  <h2>Crafting Recipes</h2>

                  <p>
                    There are many Crafting Masters around the main villages and cities. They will show you how to use
                    common materials to create armour and weapons. Doing so will increase and upgrade your Crafting
                    Level.
                  </p>
                  <p>
                    You can learn each recipe only once. If you buy the same recipe by mistake you will need to discard
                    or sell it back to the merchant so please be careful. Once you learn a recipe it will be registered
                    to your crafting list forever, even when you log out and back in the recipe will remain.
                  </p>
                  <p>
                    When crafting there will be a chance to get the standard version of what you are creating (usually
                    "Excellent" rarity) or the slightly better version (usually "Rare" rarity) with higher base stats.
                    No matter the result, your crafting level will increase by the same amount.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
