import React from 'react';
export function TradeSystem() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Trade</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>Game Store</li>
                  <li>
                    <a>Trading</a>
                  </li>
                  <li>
                    <a>Personal Shop</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggGamestore">
                  <h2>Game Shop</h2>
                  <p>
                    These traders sell basic equipment, medicine, food and other goods, players can also sell unwanted
                    items to them, or ask them to identify unknown items, or repair equipment.
                  </p>

                  <span>
                    <img src="/style/images/npc_shop_01.jpg" />
                  </span>

                  <div className="table-responsive">
                    <table className="table AddingTableBorders">
                      <tr>
                        <th style={{ width: '68' }}>Line</th>
                        <th colSpan={2}>Description</th>
                      </tr>
                      <tr>
                        <td>1</td>
                        <td colSpan={2}>
                          <span>Tip: Ctrl + Right Mouse Button</span> will increase the stack to 20{' '}
                        </td>
                      </tr>
                      <tr>
                        <td rowSpan={3}>2</td>
                        <td width="43">
                          <img width="32" height="29" src="/style/images/npc_shop_02.jpg" />
                        </td>
                        <td width="457">Repair: Repairs the durability of a single item. </td>
                      </tr>
                      <tr>
                        <td width="43">
                          <img width="32" height="29" src="/style/images/npc_shop_03.jpg" />
                        </td>
                        <td width="457">Repair all: Repairs the durability of all equipped items. </td>
                      </tr>
                      <tr>
                        <td width="43">
                          <img width="32" height="29" src="/style/images/npc_shop_04.jpg" />
                        </td>
                        <td width="457">Identify Item: Identification of an unknown item </td>
                      </tr>
                      <tr>
                        <td rowSpan={2}>3</td>
                        <td width="43">
                          <img width="18" height="15" src="/style/images/npc_shop_05.gif" />
                        </td>
                        <td width="457">Increase Number: Adds an item by one. </td>
                      </tr>
                      <tr>
                        <td width="43">
                          <img width="18" height="15" src="/style/images/npc_shop_06.gif" />
                        </td>
                        <td width="457">Decrease Number: Reduces an item by one. </td>
                      </tr>
                    </table>
                  </div>

                  <p>
                    In addition to the Merchants, the game also has various vending machines and capsule machines.
                    Vending machines and merchants are the same kind, but other machines need special coins or points
                    and give random items when the "draw" button is pressed.
                  </p>
                  <p>
                    Also in some cities, some businessmen sell more special and other items, but they require some bonus
                    to buy. Please pay special attention to before buying.
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggTrading">
                  <h2>Trading</h2>

                  <p>
                    In the game there are many ways to get rid of unwanted or spare items. One way is to trade with
                    other players.
                  </p>

                  <img src="/style/images/trade01.jpg" />

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td width="120" rowSpan={2}>
                          <img src="/style/images/trade02.jpg" width="118" height="264" />
                        </td>
                        <td width="250" height="82">
                          Right-click on the target or click on the player name button showing on the top as shown in
                          the image above and when the menu pops up select "Trade".
                        </td>
                      </tr>
                      <tr>
                        <td width="250" valign="top">
                          Press the OK Button to confirmation, after this you can't make any more changes. Once both
                          sides have confirmed the transaction will be complete.
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>

              <div className="content">
                <div id="ggPersonalShop">
                  <h2>Private Shop</h2>

                  <p>
                    In addition to direct trade with other players in the game you can also open the private store,
                    selling a variety of items.
                  </p>

                  <h4>How to open a Private Shop</h4>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td width="229">
                          <img src="/style/images/trade_03.jpg" width="200" />
                        </td>
                        <td width="371" valign="top">
                          <p>
                            Within the Skill menu {'>'} Action Skills, click on "Personal store" to open a shop, it will
                            be temporarily in "preparation" state. At this time other players can`t buy something.
                          </p>
                        </td>
                      </tr>
                      <tr>
                        <td>
                          <img src="/style/images/trade_04.jpg" />
                        </td>
                        <td valign="top">
                          <p>
                            Place your items (and amount you want to sell) into the slots. After setting, press the
                            button start selling.
                          </p>
                          <p>
                            You can also sell a variety of stack from a item if you press press Ctrl + left mouse button
                            at the item.
                          </p>
                        </td>
                      </tr>
                      <tr>
                        <td>
                          <img src="/style/images/trade_05.jpg" width="200" />
                        </td>
                        <td valign="top">
                          <p>
                            You can set a store name, as well a short comment to attract buyers. In this interface you
                            will not be able to open the shop.
                          </p>
                        </td>
                      </tr>
                    </table>
                  </div>

                  <h4>Buying and bargaining</h4>
                  <p>
                    If you want to buy personal items from other player store, just right-click at a personal shop, a
                    interface will open, and there you can purchase them.
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td width="229">
                          <img src="/style/images/trade_06.jpg" width="202" height="224" />
                        </td>
                        <td width="371" valign="top">
                          <p>
                            When the lower-right corner of the item have shown a yellow bargaining symbol, it means the
                            seller is willing to accept bargain. Click on the icon will send bargaining requirements.
                          </p>
                        </td>
                      </tr>
                      <tr>
                        <td>
                          <img src="/style/images/trade_07.jpg" width="202" height="280" />
                        </td>
                        <td valign="top">
                          <p>
                            When the seller agrees to bargaining, a new bargaining window will open for both sides,
                            where you can talk one on one, and the seller can directly modify the amount and the buyer
                            can directly press the "Buy Now" button to buy the items.
                          </p>
                          <p>
                            While bargaining, other players are temporarily unable to purchase, until the end of the
                            bargain.
                          </p>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
