import React from 'react';
export function CombatSystem() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Combat System</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>General Combat</li>
                  <li>
                    <a>Free Battle</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggGeneralCombat">
                  <h2>General Combat</h2>

                  <p>
                    Combat plays a very essential role in "Dragon Ball Online Global", it tests the player's mastery of
                    their skills. The following describes how the battle system works.
                  </p>

                  <h4>Combat Mode</h4>
                  <p>Whether it's monsters or players, the combat system is the same.</p>

                  <ol>
                    <li>
                      Selecting Target: To select a target, the player must first locate and move close to their target.
                      You can use the left mouse button, or use the TAB key to select target.
                    </li>
                    <li>
                      Basic Attack: After selecting the target right-click on the mouse, or press "F" on the keyboard,
                      and it will automatically start doing basic attack.{' '}
                    </li>
                    <li>
                      Skill Attack: After selecting the target, right-click the skills or press the key on your
                      hotbarskill on to launch it.
                    </li>
                  </ol>

                  <span>
                    <img src="./style/images/combat_01.jpg" width="500" />
                  </span>

                  <p>If you are in a team, you can see an Aggro list as shown in the picture.</p>

                  <h4>Attack Category</h4>
                  <p>
                    There are two kinds of combat attacks, physical and energy based. Close combat unarmed or with
                    gloves will fall into physical category. Energy attacks belong to long-range attacks, most of the
                    weapons that launch a long-range attack will fall into this category.
                  </p>
                  <p>
                    To minimize physical damage you need to improve physical defense, and to minimize energy damage you
                    need to improve energy defence.
                  </p>

                  <h4>Knock Down</h4>
                  <p>
                    If a character is knocked down during combat, they will be unable to carry out any actions untill
                    they stand up again. While knocked down the player will take more damage. In order to stand up
                    quickly after being knocked down, you need to quickly move the mouse from side to side.
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggFreebattle">
                  <h2>Free Battle</h2>

                  <p>
                    In the game, players can freely fight against others in a one vs one, this is a good way to
                    understand their own fighting skills.
                  </p>
                  <p>
                    To start a free battle, both sides must agree (one send the request, other player agrees). When the
                    battle starts, a referee (NPC) will appear in the middle, who watches over a certain area range. If
                    you leave this area you have 10 seconds to return, otherwise you will loose.
                  </p>

                  <img src="./style/images/combat_02.jpg" width="500" />

                  <p>
                    A free battle has no special rules and can be done in many different areas. If the player's LP
                    becomes 0, the battle will end.
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tbody>
                        <tr>
                          <th style={{ width: '15%' }}>Winning Conditions</th>
                          <th>Content</th>
                        </tr>
                        <tr>
                          <td>KO</td>
                          <td>Player LP reach 0</td>
                        </tr>
                        <tr>
                          <td>Leaving The Range</td>
                          <td>If the player gets out of the battle area range and don't return in a certain time.</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>

                  <p>After a free battle, the loser doesn't die, but his LP becomes 1.</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
