import React from 'react';
import './layoutBox.less';

interface Props {
  title: string;
  imgSrc: string;
  h1Text: string;
  h2Text: string;
}

export function LayoutBox({ title, imgSrc, h1Text, h2Text }: Props) {
  return (
    <div className="layout-box-top">
      <img className="layout-boxBc" src={imgSrc} alt="" />
      <div className="layout-box-cover"></div>
      <div className="layout-box-text">
        <div className="layout-box-text-title">{title}</div>
        <h1>{h1Text}</h1>
        <h2>{h2Text}</h2>
      </div>
    </div>
  );
}
