import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { iconList, MenuItem, changeLanguage, overlayList } from './constants';
import { Dropdown, Menu, Space } from 'antd';
import { useTranslation } from 'react-i18next';
import './header.less';

const renderMenu = (list: MenuItem[]) => {
  return list.map((menu, i) => (
    <Link className="menu-item" key={i} to={menu.path}>
      {menu.name}
    </Link>
  ));
};

export const Header = () => {
  const headerContent = useRef<HTMLDivElement>();
  const [fixedShow, setFixedShow] = useState(false);
  const [language, setLanguage] = useState('English');
  const { t } = useTranslation();
  const i18nmenuList: MenuItem[] = t('header.menuList', { returnObjects: true });
  const locLanguage = localStorage.getItem('language');

  useEffect(() => {
    if (locLanguage) {
      changeLanguage(locLanguage);
      if (locLanguage === 'en') {
        setLanguage('English');
      } else if (locLanguage === 'zh') {
        setLanguage('中文');
      }
    }
  }, []);

  const overlay = (
    <Menu
      items={[
        ...overlayList.map((item, i) => {
          return {
            label: item.title,
            key: i,
            onClick: () => {
              changeLanguage(item.language);
              if (item.language === 'en') {
                setLanguage('English');
              } else if (item.language === 'zh') {
                setLanguage('中文');
              }
            }
          };
        })
      ]}
    />
  );

  useEffect(() => {
    const getHeaderScrollTop = () => {
      const { top } = headerContent.current.getBoundingClientRect();
      setFixedShow(top <= 0);
    };
    window.addEventListener('scroll', getHeaderScrollTop);
    return () => window.removeEventListener('scroll', getHeaderScrollTop);
  }, []);

  return (
    <header className="header">
      <div className="header-box header-top  ">
        <div className="header-content flex space-between">
          <div className="welcome">{t('header.title')}</div>
          <div className="flex">
            <div className="icon-box">
              {iconList.map((icon, i) => (
                <a target='blank' href={icon.href}>
                  <img className="icon" key={i} {...icon} alt="icon" />
                </a>
              ))}
            </div>
            <Dropdown overlay={overlay}>
              <Space>
                <div className="colorfff">{language}</div>
              </Space>
            </Dropdown>
          </div>
        </div>
      </div>
      <div className="header-box herder-bottom ">
        <div
          ref={(el) => (headerContent.current = el)}
          id="header-content"
          className="header-content flex space-between"
        >
          <div className="header-bottom-nav flex">{renderMenu(i18nmenuList.slice(0, 4))}</div>
          <div className="header-bottom-nav flex">{renderMenu(i18nmenuList.slice(4))}</div>
        </div>
      </div>
      <div className="background" />
      <div className="logo" />
      <div className="fixedNav" style={{ height: (fixedShow ? 70 : 0) + 'px' }}>
        <div className="header-content flex fixedNav-box">
          <div className="fixedNav-logo"></div>
          <div className="flex">
            <div>{renderMenu(i18nmenuList)}</div>
            <Dropdown overlay={overlay}>
              <Space>
                <div className="colorfff">{language}</div>
              </Space>
            </Dropdown>
          </div>
        </div>
      </div>
    </header>
  );
};
