import twitter from '../../static/images/twitter.png';
import discord from '../../static/images/discord.png';
import telegram from '../../static/images/telegram.png';
import i18n from '../../i18n/config';

export type MenuItem = {
  path: string;
  name: string;
};
export type overlayItem = {
  title: string;
  language: string;
};

export const iconList = [
  {
    src: twitter,
    href: 'https://twitter.com/DRAGONBALLGAFI'
  },
  {
    src: discord,
    href: ''
  },
  {
    src: telegram,
    href: 'https://t.me/DragonBallCoinMoon'
  }
];

export const changeLanguage = (val) => {
  i18n.changeLanguage(val); // val入参值为'en'或'zh'或'jp'
  localStorage.setItem('language', val);
};
export const overlayList:overlayItem[] = [
  {
    title: 'English',
    language: 'en'
  },
  {
    title: '中文',
    language: 'zh'
  },
  {
    title: '日本語',
    language: 'jp'
  }
];
