import React from 'react';
import './style.less';

export function QAitem(props: { question: string; answer: string }) {
  const { question, answer } = props;
  return (
    <div className="qa-item-box">
      <div className="issue">{question}</div>
      <div className="cut-off-rule"></div>
      <div className="answer">{answer}</div>
    </div>
  );
}
