import React from 'react';
import './gameGuide.less';
import { Routes, Route } from 'react-router-dom';
import { LeftNav } from './components/leftNav';
import { Characters } from './components/characters';
import { Dungeons } from './components/dungeons';
import { Skills } from './components/skills';
import { Storyline } from './components/storyline';
import { Community } from './components/community';
import { TradeSystem } from './components/tradeSystem';
import { NpcS } from './components/npcs';
import { CombatSystem } from './components/CombatSystem';
import { DragonBalls } from './components/DragonBalls';
import { RankingSystem } from './components/RankingSystem';
import { Crafting } from './components/Crafting';
import { Movesystem } from './components/movesystem';
import { WorldSystem } from './components/WorldSystem';
import { DojoSystem } from './components/DojoSystem';
import { NotData } from './components/notData';
import { BudokaiSystem } from './components/BudokaiSystem';

export function GameGuide() {
  return (
    <div className="gameGuide-box">
      <div className="gameGuide-content">
        <LeftNav />
        <div className="right-form">
          <Routes>
            <Route path="/" element={<Characters />} />
            <Route path="/characters" element={<Characters />} />
            <Route path="/dungeons" element={<Dungeons />} />
            <Route path="/skills" element={<Skills />} />
            <Route path="/storyline" element={<Storyline />} />
            <Route path="/community" element={<Community />} />
            <Route path="/tradeSystem" element={<TradeSystem />} />
            <Route path="/NPCs" element={<NpcS />}></Route>
            <Route path="/combatSystem" element={<CombatSystem />}></Route>
            <Route path="/dombatSystem" element={<DragonBalls />}></Route>
            <Route path="/RankingSystem" element={<RankingSystem />}></Route>
            <Route path="/Crafting" element={<Crafting />}></Route>
            <Route path="/MoveSystem" element={<Movesystem />}></Route>
            <Route path="/WorldSystem" element={<WorldSystem />}></Route>
            <Route path="/DojoSystem" element={<DojoSystem />}></Route>
            <Route path="/BudokaiSystem" element={<BudokaiSystem />}></Route>
            <Route path="*" element={<NotData />} />
          </Routes>
        </div>
      </div>
    </div>
  );
}
