import React from 'react';
export function BudokaiSystem() {
  return (
    <div id="wrapper">
      <div id="navigation">
        <div id="page-body">
          <div className="container">
            <div className="text-center title">Budokai</div>

            <div className="GameGuideContainer">
              <div className="navigation">
                <ul>
                  <li>Ranked Battle</li>
                  <li>
                    <a>World Martial Art</a>
                  </li>
                  <li>
                    <a>AIR Capsule Machine</a>
                  </li>
                </ul>
              </div>

              <div className="content">
                <div id="ggRankedBattle">
                  <h2>Ranked Battle</h2>
                  <p>
                    In order to learn your className's pros and cons, you must test in combat! Players can always fight
                    one another in Ranked Battles, providing you are of a similar level. You must win ranked matches if
                    you wish to earn your place in the World Martial Arts Tournament.
                  </p>
                  <p>
                    Anyone can apply to take part in a Ranked Battle. Simply talk to the "Ranked Battle Probe" infront
                    of a dojo gate or near the dojo Merchants. Each player can attend up to 80 Ranked matches a day.
                    Once you apply you'll need to wait until another player has also signed up for a Ranked Battle who
                    is a similar level to your own. A prompt will appear on screen to which you can either accept or
                    deny the invitation to battle. If you accept you'll be automatically teleported to the battle venu
                    which is where the fun starts!
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td>
                          <img src="./style/images/ranked01.jpg" width="200" />
                        </td>
                        <td>
                          <img src="./style/images/ranked02.jpg" width="200" />
                        </td>
                      </tr>
                    </table>
                  </div>

                  <p>
                    There are multiple stages for Ranked Battles. You can select which one you wish to fight on when
                    talking to the Ranked Battle Probe. You can also select between Individual Battles or Team Battles.
                  </p>

                  <div className="table-responsive">
                    <table className="table">
                      <tr>
                        <td className="label-col">Single Battles</td>
                        <td>
                          Battles will last a duration of 2 whole minutes and consist of a best-of-3 setup. The first to
                          win 2 matches will be declared the winner! To win a match, players will need to knock their
                          rivals out of the ring or reduce their LP to 0, causing them to faint.
                        </td>
                      </tr>
                      <tr>
                        <td className="label-col">Team Battle</td>
                        <td>
                          Battles will last a duration of 3 whole minutes and consist of a best-of-5 setup. The first
                          team to win 3 matches will be declared the winners!
                        </td>
                      </tr>
                    </table>
                  </div>

                  <div className="OneTwo">
                    <span>
                      <img src="./style/images/ranked03.jpg" width="300" style={{ marginRight: '5px' }} />
                      <img src="./style/images/ranked04.jpg" width="300" style={{ marginRight: '5px' }} />
                    </span>
                  </div>

                  <div className="OneTwo">
                    <div className="left">
                      <img src="./style/images/ranked05.jpg" />
                    </div>

                    <div className="right">
                      <p>
                        After each match, a point will be added to the side who won that respective round. When all
                        matches are over the final score will show the winner and reward the players with their
                        well-earned Mudosa Points. These are essential if you want to be considered for the World
                        Martial Arts Tournament. After participating in the World Martial Arts Tournament players will
                        then need to participate in Ranked Battled once again to earn their place in the next tourney!
                        In addition, Contestants can also receive bonus points as a reward for outstanding technique in
                        fights.
                      </p>
                    </div>
                  </div>
                </div>
              </div>

              <div className="content">
                <div id="ggWorldmartialart">
                  <h2>World Martial Art</h2>

                  <p>
                    The World Martial Arts Tournament is a long-standing tradition stretching hundreds of years back,
                    All challengers eagerly look forward to participating. The event will be held tiwce a week, and the
                    registration will automatically open. The Child Tournament will be held every{' '}
                    <strong>Tuesday 2 pm (UTC +2)</strong> and the Adult Tournament will be held every{' '}
                    <strong>Saturday 6 pm (UTC+2).</strong> Want to prove you're one of the strongest players on the
                    server? Be sure to attend!
                  </p>

                  <span>
                    <img src="./style/images/budo_01.jpg" width="500" />
                  </span>

                  <p>
                    The event will be divided into two separate groups. Players level 29 and lower can participate in
                    the Child Division, while players level 30 and higher will participate in the Adult Division. Before
                    the start of the World Martial Arts Tournament, the registration system will appear in the lower
                    right hand side to prompt players to apply. If you wish to partake in the event you must apply
                    through the application icon in the lower left of the screen.
                  </p>

                  <img src="./style/images/budo_02.jpg" />
                  <img src="./style/images/budo_03.jpg" width="300" />

                  <div className="table-responsive">
                    <table className="table AddingTableBorders">
                      <tr>
                        <th style={{ width: '12%' }}></th>
                        <th>Single Tournament</th>
                        <th>Party Tournament</th>
                      </tr>
                      <tr>
                        <th>Preliminaries</th>
                        <td>
                          Out of all the players who apply, only the top 480 will be granted access to the preliminaries
                          to fight for a place in the tournament. Each group will consist of 15 players and a total of
                          32 will be selected to fight in the semi-finals. Good luck!
                        </td>
                        <td>
                          Team Preliminaries will be made up of the 75 teams with the highest combined ranking, each
                          team can consist of up to 5 players and each group of preliminaries will be made up of 5 teams
                          all fighting for a chance to participate in the semi-finals. A total of 15 teams will be
                          selected to fight. Good luck!
                        </td>
                      </tr>
                      <tr>
                        <th>Matchups</th>
                        <td>
                          Once the preliminaries are over, those who were successful will be sorted into the Tournament
                          Table. This schedule will show who you are up against in your next match. After each match,
                          the table will be updated with the winners and losers. The top four players will then enter
                          the Semi-finals.
                        </td>
                        <td>
                          Once the preliminaries are over, those who were successful will be sorted into the Tournament
                          Table. This schedule will show who you are up against in your next match. After each match,
                          the table will be updated with the winners and losers. The top four teams will then enter the
                          Semi-finals.
                        </td>
                      </tr>
                      <tr>
                        <th>Semi-finals</th>
                        <td>
                          The fight for the title of "Champion" begins! Players will be divided into two groups. Whoever
                          wins their best-out-of-3-match will then move onto the finals!
                        </td>
                        <td>
                          The fight for the title of "Champion" begins! Teams will be divided into two groups. Whoever
                          wins their best-out-of-3-match will then move onto the finals!
                        </td>
                      </tr>
                      <tr>
                        <th>Finals</th>
                        <td>
                          This is it, the match to decide the winner of the World Martial Arts Tournament will take
                          place! This fight will determine who the champion is. Both the finalist and the semi finalists
                          will be awarded Commemorative coins which can be used to buy items at the Tournament Merchants
                          and decorative titles showing off their achievements.
                        </td>
                        <td>
                          This is it, the match to decide the winning team of the World Martial Arts Tournament will
                          take place! This fight will determine who the champions are. Both the finalists and the semi
                          finalists will be awarded Commemorative coins which can be used to buy items at the Tournament
                          Merchants and decorative titles showing off their achievements.
                        </td>
                      </tr>
                    </table>
                  </div>

                  <p>
                    In the World Martial Arts tournament, each round will reward players with Mudosa points and
                    restorative items, Half Senzu Beans (Child Players) and regular Senzu Beans (Adult Players).
                  </p>
                </div>
              </div>

              <div className="content">
                <div id="ggAIRCapsuleMachine">
                  <h2>Capsule Machine</h2>

                  <p>
                    Winning Ranked Battles, and matches in the World Martial Arts Tournament, will reward your character
                    with Mudosa Points. You can use these points to gamble with the AIR Capsule Machine or trade them to
                    the Martial Arts Businessmen for Accessories, Weapons, Scouters and other Props.
                  </p>

                  <h2>AIR Capsule Machine</h2>
                  <p>
                    Each main city and town will have at least one AIR Capsule Machine. The number on the AIR Capsule
                    Machine will represent what level of item you can win from using it. For example when you use the
                    AIR Capsule Machine CL-50 you will receive an item suitable for players at level 50 or higher.{' '}
                  </p>
                  <img src="./style/images/machinebudo01.jpg" width="500" />
                  <p>
                    To use the machine twist on the dial. The googly eyes on top will also comically twirl around to
                    amuse you. Out will pop an egg capsule! Open it to find out what you've won.
                  </p>

                  <h2>Martial Arts Businessmen</h2>
                  <p>
                    The starting villages, West City, and all major towns boast impressive Dojos. Martial Arts
                    Businessmen settle near and around these places to sell exclusive Weapons, Clothing, Gear,
                    Accessories, Jewellery, and other Props. They only accept Mudosa Points as currency. There are also
                    special Merchants located inside of Mudosa Village. You'll need to become a champion to buy their
                    wares.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
