import React from 'react';
import './ImgCard.less';
export interface ImgCardProps {
  src: string;
  href?: string;
  context?: string;
} 

export const ImgCard = ({ src, href, context = 'Dragon Ball COIN' }: ImgCardProps) =>{
  return(
    <a href={href} className="img-card">
      <div className="flip">
        <div className="front">
          <img src={src} alt={context} />
          {/* <p>{context}</p> */}
        </div>
        <div className="back" style={{ background: `url(${src}) no-repeat center`, backgroundSize: 'cover' }}>
          <div className="cover">
            <p className="img-catd-text">{context}</p>
          </div>
        </div>
      </div>
    </a>
  )
  
}