import React from 'react';
import { Carousel } from 'antd';
import { CarouselItem } from './components/CarouselItem/CarouselItem';
import { ImgCard } from '../../components/ImgCard/imgCard';
import { Typewriter } from '../../components/Typewriter/typewriter';
import { PageTitle } from '../../components/pagesTitle/pageTitle';
import { QAitem } from './components/QAitem/QAitem';
import thumbnail from '../../static/images/dbtb-game-thumbnail.png';
import thumbnail2 from '../../static/images/dbf_game_thumb_408x314_2.png';
import thumb from '../../static/images/dbzk_game_thumb_408x314.png';
import { QAListItem, typewriterList, partnersList } from './constants';
import { useTranslation } from 'react-i18next';
import videoPoster from '../../static/images/31-dbfz_launch.png';
import { Player, BigPlayButton } from 'video-react';
import { CarouselList } from './constants';
import './home.less';

export function Home() {
  const [index, setIndex] = React.useState(0);
  const { t } = useTranslation();
  const i18nTypewriterList = typewriterList.map((item, i) => {
    return {
      ...item,
      context: t('home.typewriterList', { returnObjects: true })[i]
    };
  });
  const QAList: QAListItem[] = t('home.QAList', { returnObjects: true });

  return (
    <main>
      <div className="carousel-box">
        <Carousel autoplay afterChange={setIndex}>
          {CarouselList.map((item, i) => {
            return <CarouselItem key={i} imgUrl={item} activeIndex={i === index}></CarouselItem>;
          })}
        </Carousel>
        <div className="pagers-curve"></div>
      </div>

      <div className="video-box">
        <PageTitle title={t('home.teaserTitle')}>{t('home.teaser')}</PageTitle>
        <div className="video-cover">
          <Player poster={videoPoster} playsInline src="http://54.67.65.203/test1.mp4">
            <BigPlayButton position="center" />
          </Player>
        </div>

        {/* <div className="video-cover">
          <div className="play-btn">
            <img className="play-img" src={play} alt="" />
          </div>
        </div> */}
      </div>
      <div className="welcome">
        <PageTitle title={t('home.welcomeText1')}>{t('home.welcomeTitle')} </PageTitle>
        <div className="introduce-img-card-box flex">
          <div className="img-card-box">
            <ImgCard src={thumbnail} context={t('dragonballNFT')}></ImgCard>
          </div>
          <div className="img-card-box">
            <ImgCard src={thumbnail2} context={t('dragonballNFT')}></ImgCard>
          </div>
          <div className="img-card-box">
            <ImgCard src={thumb} context={t('dragonballNFT')}></ImgCard>
          </div>
        </div>
        <div className="introduce-text-box">
          <div className="introduce-text">
            <p>{t('home.topIntroduction')}</p>
            <p>{t('home.bottomIntroduction')}</p>
          </div>
          <div className="myBtn">{t('home.aboutus')}</div>
        </div>
      </div>
      <div className="welcome">
        <PageTitle title={t('home.corm')}>{t('home.cormTitle')}</PageTitle>
        <div className="inner-img-box inner-box flex flexWard">
          {i18nTypewriterList.map((item, i) => (
            <div key={i} className="inner-box">
              <Typewriter {...item} />
            </div>
          ))}
        </div>
        <PageTitle title={t('home.partners')}></PageTitle>
        <div className="partners-list flexWard flex">
          {partnersList.map((item, i) => (
            <div key={i} className="partners-item">
              <img src={item} alt="" />
            </div>
          ))}
        </div>
      </div>
      <div className="support">
        <PageTitle title={t('home.securedTitle')}>{t('home.securedText')}</PageTitle>
        <img className="support-img" src="https://monster-battle.games/images/partners_color/25.svg" alt="" />
      </div>

      <div className="QABox">
        <PageTitle title={t('home.QATitle')}>{t('home.QAText')}</PageTitle>
        <div className="QAList flex flexWard">
          {QAList.map((item, i) => (
            <div key={i} className="QAitem ">
              <QAitem question={item.question} answer={item.answer}></QAitem>
            </div>
          ))}
        </div>
      </div>
    </main>
  );
}
